import URLToolkit from 'url-toolkit';

// tsurl
export function handleTSUrl(url, matched = false) {
    if (!matched) {
        return url.split("?")[0];
    }
    return url;
}

// 
export function throttle(method, context, colddown = 15) {

    var going = false;
    return function () {
        if (going) return;
        going = true;
        setTimeout(function(){
            method.call(context);
            going = false;
        }, colddown*1000);
    }
};

// channelId generator
export function defaultChannelId(url, protocol, browserInfo = {}) {
    const path = URLToolkit.parseURL(url).path.split('.')[0];
    return `${path}[${protocol}]`;
}



